import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { Secciones, Post} from './Data'

@Injectable({
  providedIn: 'root'
})
export class DataService {
  constructor(private http: HttpClient){}

  secciones = './assets/secciones.json';
  banners = './assets/banners.json';
  posts = './assets/posts.json';

  getSecciones(){
    return this.http.get<Secciones>(this.secciones);
  }
  getBanners(){
    return this.http.get<Response>(this.banners);
  }
  getPosts(){
    return this.http.get<Response>(this.posts);
  }
}
