import { Component, OnInit, HostListener} from '@angular/core';
import { Router, ActivatedRoute, Params } from '@angular/router';
import { switchMap } from 'rxjs/operators';
import { DataService } from '../data-service.service';
import { Secciones, Post } from '../Data'

@Component({
  selector: 'app-home',
  templateUrl: './home.component.html',
  styleUrls: ['./home.component.css']
})
export class HomeComponent implements OnInit {
  constructor(private dataService: DataService,  
    private route: ActivatedRoute,
    private router: Router) {
    } 

  seccion_id : string;
  seccion : string;
  banners : {id:string,post:Post}[] = [];

  ngOnInit() {
    //Escuchar el cambio de parámetro en la ruta (cambio de seccion)
    //route.snapshot.paramMap.get('id')
    this.route.params.subscribe((params: Params) => {
      if(params['id'] != null){
        this.dataService.getSecciones().subscribe((secciones: Secciones) =>{
          if(secciones.ids[params['id']]){          
            this.seccion = secciones.ids[params['id']];
            console.log("Cambio de seccion: " + this.seccion)
          } 
          else{
            this.router.navigate(['/home'])
          } 
        });
      }
      this.getBanners();
    });
  }

  getBanners(){
    this.banners = [];
    this.dataService.getBanners().subscribe((res: Response) => {
      var banners_index = res["Banners"];     
      this.dataService.getPosts().subscribe((posts: Response) => {
        for (var i = 0; i < banners_index.length; i++) {
          if(!this.seccion || this.seccion && posts[banners_index[i]].Seccion == this.seccion){
            this.banners.push({id:banners_index[i],post:posts[banners_index[i]]});     
          }                             
        }
      });
    });
  }
}
